<script>
  import {api} from "@/env";
  import axios from "axios";
  import {version,date} from "../../package.json"

  export default {
    data() {
      return {
        version: {
          webVersion: version,
          webDate: date,
          apiURL: api,
          apiVersion: ""
        }
      }
    },
    created(){
      axios.get(api+"/openapi.json").then(response => {
        this.version.apiVersion = response.data.info.version
      })
    }
  }
</script>

<template>
  <div class="version">

    <div class="header">
      <img src="../../static/eosc-synergy.png" width="100px" alt="EOSC Synergy">
      <div class="title">SQAaaS</div>
    </div>

    <div class="version_grid">
      <div>Web Version</div> <div>{{version.webVersion}}</div>
      <div>Web Date</div>    <div>{{version.webDate}}</div>
      <div>API URL</div>     <div>{{version.apiURL}}</div>
      <div>API Version</div> <div>{{version.apiVersion}}</div>
    </div>
  </div>
</template>

<style scoped>
  .version{
    padding-top: 0.5em;
    padding-left: 2em;
  }
  .version .header{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    column-gap: 1em;
  }
  .version .header img{
    margin-left: 0.6em;
  }
  .version .header .title{
    font-size: 1.5em;
    font-weight: bold;
    color: #407bca;
  }
  .version_grid{
    display: grid;
    grid-template-columns: max-content max-content;
  }
  .version_grid div{
    padding: 0.2em 1em;
  }
  .version_grid div:nth-child(odd){
    background-color: #f0f0f0;
    border-right: solid 1px grey;
  }
  .version_grid div:nth-child(even) {
    background-color: #fafafa;
  }
  .version_grid div:nth-child(1),
  .version_grid div:nth-child(2){
    padding-top: 0.5em;
  }
  .version_grid div:nth-child(3) {
    padding-bottom: 0.7em;
  }
  .version_grid div:nth-child(7),
  .version_grid div:nth-child(8){
    padding-bottom: 0.5em;
  }
</style>
