import Vue from 'vue'
import VueRouter from 'vue-router'
import VueGtag from "vue-gtag";
import App from './App.vue'
import env from './env.js'
import store from "./store"
import VueSweetalert2 from 'vue-sweetalert2';
import "skeleton-screen-css"

// If you don't need the styles, do not connect
import 'sweetalert2/dist/sweetalert2.min.css';

// LightBootstrap plugin
import LightBootstrap from './light-bootstrap-main'

// router setup
import routes from './routes/routes'

import './registerServiceWorker'
// plugin setup
Vue.use(VueRouter)
Vue.use(LightBootstrap)
Vue.use(VueSweetalert2);

Vue.prototype.$eventHub = new Vue();

if(process.env.NODE_ENV === 'production') { // 'development'|'test'|'production'
  //let oldLog = console.log
  //console.log = function (message) { oldLog("Log -> " + message)}
  console.log = function (message) {}
}

window.axios = require('axios');
// window.axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';

window.axios.interceptors.request.use(function (config) {
  // Do something before request is sent
  // const AUTH_TOKEN = document.getElementsByName("token")["0"].content;
  var session = JSON.parse(localStorage.getItem("session"))
  const AUTH_TOKEN = session.user.access_token

  if(AUTH_TOKEN){
      config.headers.common['Authorization'] = `Bearer ${AUTH_TOKEN}`
  }
  return config;
}, function (error) {
  // Do something with request error
  console.log(error);
  this.$router.replace(this.$route.query.redirect || "/logout");
  return Promise.reject(error.response);
});

Vue.mixin({
  data: function(){
    return {
      env:env,
    }
  }
})

// configure router
const router = new VueRouter({
  routes, // short for routes: routes
  linkActiveClass: 'nav-item active',
  scrollBehavior: (to) => {
    if (to.hash) {
      return {selector: to.hash}
    } else {
      return { x: 0, y: 0 }
    }
  }
})

Vue.use(VueGtag, {
  config: { id: 'G-NN2TNNYRDB' }
  }, router
);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App),
  router,
  store,
})
